@import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

html{
    color: white;
}

a{
    text-decoration: none;
    text-decoration-color: none;
    color: aliceblue;
}

.head{
    background-color: #101010;
    min-height: 3vh;
    width: 100%;
    display:flex;
    align-items: center;
    position: fixed;
}

.container{
    min-height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 130px 0;
    background-color: #101010;
    display: grid;
    grid-gap: 1em;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, auto);
    grid-template-areas:
    'forms	  timeline      timeline'
}

.form-content{
    grid-area: forms;
    position: fixed;
    margin-left: 100px;
    bottom: 400px;
    z-index:5;
    padding: 20px;
    border-radius: 10px;
    background-color: #1e1f22;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}


.form-control{
    width: 100%;
    display: block;
    padding: 5px;
    border: 3px solid #5f41f4;
    border-radius: 10px;
    margin-bottom: 10px;
    font-size: 16px;
}

#file-img{
    background-color: #5f41f4;
}

.btn{
    background-color: #1e1f22;
    width: 100%;
    height: 40px;
    border-radius: 10px;
    border: 3px solid #5f41f4;
    color: #5f41f4;
    font-size: 20px;
}

.btn:hover{
    background-color: #5f41f4;
    border: 3px solid #1e1f22;
    color: #1e1f22;
}


.timeline{
    grid-area: timeline;
    height: auto;
    max-width: 800px;
    margin: 0 auto;
    position: relative;
    text-align: center;
    margin-right: 170px;
}

.timeline ul{
    z-index:1;
    list-style: none;
}

.timeline ul li{
    padding: 20px;
    background-color: #1e1f22;
    color: white;
    border-radius: 10px;
    margin-bottom: 20px;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
    
}

.timeline ul li:hover{
    -webkit-animation: animation 865ms linear both;
    animation: animation 865ms linear both;
}


.timeline ul li:last-child{
    margin-bottom: 0;
}

.timeline-content h1{
    font-size: 25px;
    font-weight: 500;
    line-height: 30px;
    margin-bottom: 10px;
}


.timeline-content p{
    font-size: 12px;
    line-height: 30px;
    font-weight: 300;
}

.timeline-content .solution{
    font-size: 15px;
    font-weight: 300;
    margin-bottom: 10px;
    letter-spacing: 2px;
}

.timeline-content .btn-delete{
    background-color: #5f41f4;
    border-radius: 30px;
    border: 2px solid white;
    color: white;
    padding: 10px 15px;
    margin-left: 300px;
}

.img-timiline{
    width: 100%;
    border-radius: 5px;
}

.formulario{
    width: 50%;
    height: 40px;
    font-size: 20px;
    position: fixed;
    top: 20px;
    z-index: 10;
    display: block;
    margin-left: auto;
    margin-right: auto;
    border-radius: 7px;
    color: #5f41f4;
    padding: 10px;
    border: solid 5px #5f41f4;
}


/*Animacion background*/
.block{
    position: absolute;
    width: 50px;
    height: 100px;
    border-radius: 3px;    
    background-color:#5f41f4;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;    
}

.block p{
    margin-top: 25px;
    font-size: 9px;
    text-align: center;
    
}

.img-separator{
    height: 10px;
    background-image: url("https://user-images.githubusercontent.com/73097560/115834477-dbab4500-a447-11eb-908a-139a6edaec5c.gif");
}

